<template>
    <div :class="'page-container '+mobileClass">
        <div v-if="mobile && menuShow" @click="menuShow=false" class="menuBgScreen"></div>
        <div v-if="mobile">

            <div class="live-menu" id="mobile-live-header-menu">
                <div :class="'big-menu big-'+code" @click="goMobileMain()">
                    STEP01. 내 라이브
                </div>

                <div class="big-menu">
                    STEP03. 사용 후 관리
                </div>
                <ul class="menu-ul" v-if="status == 'complete'">
                    <li v-for="(menu,index) in menuList" :class="'menu-item '+menu.active" @click="goMobileMenu(menu.code)">
                        {{menu.name}}
                    </li>
                </ul>
            </div>
            <div class="mobile-header">
                <div class="back-button" @click="menuShow=true">
                    <font-awesome-icon icon="fas fa-bars"></font-awesome-icon>
                </div>
                <div class="header-title">
                    {{ title }}
                </div>
                <div class="mypage">
                    <div class="icon-box" @click="goPage('/mypage/main')">
                        <font-awesome-icon style="font-size:12pt" icon="fa-regular fa-user"/>
                    </div>
                </div>
                <div class="play-button" @click="playLive()">
                    <font-awesome-icon icon="far fa-play-circle"></font-awesome-icon>
                </div>

            </div>
            <div class="virtual-box" style="height:70px;"></div>
            <router-view></router-view>
        </div>
        <div v-else>
            <div class="setting-header">
                <div class="header-container">
                    <div class="logo" @click="goPage('/')">
                        <img src="../../../../assets/images/logo.png">
                    </div>
                    <div class="menu-pannel">
                        <ul class="menu">
                            <li class="menu-item">
                                <div class="icon-box" style="cursor: pointer" @click="goPage('/mypage/main')">
                                    <font-awesome-icon style="font-size:16pt" icon="fa-regular fa-user"/>
                                </div>
                            </li>
                            <li class="menu-item">
                                <v-btn class="start-button" @click="playLive()" rounded><span>시작하기</span></v-btn>
                            </li>

                        </ul>
                    </div>
                </div>

            </div>
            <div class="setting-body">
                <div class="setting-menu">
                    <ul class="step-menu-ul">
                        <li v-for="(menu,index) in menuList">
                            <div class="step-menu-item">STEP0{{index+1}}. {{menu.name}}</div>
                            <ul class="menu-ul">
                                <li v-for="(item,subIndex) in menu.subList">
                                    <div :class="'menu-item '+item.active" @click="setMenu(item.code,item.active)">
                                        {{item.name}}
                                        <div v-if="item.code=='dropDown'">
                                            <font-awesome-icon v-if="item.active" class="angle-icon"
                                                               icon="fas fa-angle-up"/>
                                            <font-awesome-icon v-else class="angle-icon" icon="fas fa-angle-down"/>
                                        </div>
                                    </div>
                                    <ul class="sub-menu-ul" v-if="item.code=='dropDown' && item.active">
                                        <li :class="'sub-menu-item '+subItem.active" v-for="subItem in item.subList"
                                            @click="setSubMenu(subItem.code)">
                                            {{subItem.name}}
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
                <div class="setting-content">
                    <router-view></router-view>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
    import AUTH from "@/config/web/auth";
    import {mapGetters, mapActions} from "vuex";
    import JQ from "jquery";
    import apiService from "@/config/web/apiService";
    import {API_URL_BROADCAST_LIVE_INFO, API_URL_BROADCAST_CHECK_LIVE_INFO} from "@/config/web/urls";

    export default {
        watch: {
            '$route'(to, from) {
                // this.setActive(to.name);
            },
            menuShow(val) {
                if (val) {
                    JQ('body').css({'height': JQ(window).height() + 'px', 'overflow': 'hidden'})
                    JQ('#mobile-live-header-menu').animate({
                        left: 0,
                    }, 300)

                } else {
                    let left = -1 * (JQ(window).width() - 90);
                    JQ('#mobile-live-header-menu').animate({
                        left: left + 'px',
                    }, 100, function () {
                        JQ('body').css({height: 'auto', overflow: 'visibile'})
                    })
                }
            }
        },
        computed: {
            ...mapGetters('broadcast', ["getGno"]),

        },

        data() {
            return {
                DEBUG: ((process.env.NODE_ENV == 'development') ? true : false),
                menuList: [],
                mobileClass: '',
                code: 'info',
                status: 'ready',
                id: '',
                subMenuList: [],
                mobile: this.$isMobile(),
                menuShow: false,
                title: '',
            }
        },
        created() {

            const member = AUTH.getSession();
            if (!member || !member?.memberInfo) {
                this.$router.push('/');
                return;
            }
            if (!this.getGno) {
                this.$router.push('/');
                return;
            }
            if (this.$isMobile()) {
                this.mobileClass = 'mobile';
            }

            this.id = this.getGno;
            this.getMeetingInfo();

        },
        methods: {
            ...mapActions('broadcast', ["setBroadcastInfo", "setBroadcastInfoActive"]),
            getMeetingInfo() {
                let frm = {no: this.id.toString()};
                apiService.post(API_URL_BROADCAST_LIVE_INFO, frm, (res) => {
                    if (!res.subject) {
                        this.$router.push('/');
                        return;
                    }

                    this.status = res.infoStatus;
                    this.role = res.role;
                    this.title = res.subject
                    this.setMenuList();
                    this.setActive(this.$route.name)

                    this.setBroadcastInfo({
                        subject: res.subject,
                        logoName: res.logoName,
                        userUrl: location.protocol + '//' + location.host + '/liveStream/userLive/intro/' + this.id,
                        status: res.infoStatus,
                        role: res.role
                    });
                    this.setBroadcastInfoActive(true);
                });
            },
            setMenuList() {
                if (this.mobile) {
                    this.menuList = [
                        {name: '시험 내역', active: '', code: 'afterExam'},
                        {name: '간단 퀴즈 내역', active: '', code: 'afterQuiz'},
                        {name: '채팅 내역', active: '', code: 'afterChat'},
                        {name: '설문조사 내역', active: '', code: 'afterPoll'},
                        {name: '투표 내역', active: '', code: 'afterVote'},
                        {name: '공유 내역', active: '', code: 'afterData'},
                        {name: '게시판 내역', active: '', code: 'afterBoard'},
                        {name: '참가자 정보', active: '', code: 'afterUser'},
                    ]
                } else {
                    const active = (this.status != 'complete') ? 'disable' : '';
                    this.menuList = [
                        {
                            name: '내 라이브',
                            subList: [
                                {name: '내 라이브 정보', active: 'active', code: 'main'},
                                {name: '환경설정', active: '', code: 'env'},
                            ]
                        },
                        {
                            name: '사용 전 관리',
                            subList: [
                                {
                                    name: '시험설정', active: '', code: 'dropDown', subList: [
                                        {name: '일반 설정', active: '', code: 'examSet'},
                                        {name: '수기로 입력', active: '', code: 'examDirect'},
                                        {name: '엑셀 업로드', active: '', code: 'examUpload'},
                                    ]
                                },
                                {name: '문제/자료 관리', active: '', code: 'dataList/all'},
                                {name: '설문조사 관리', active: '', code: 'pollList'},
                                {name: '투표 관리', active: '', code: 'voteList'},
                                {name: '게시판 관리', active: '', code: 'boardSetting'},
                            ]
                        },
                        {
                            name: '사용 후 관리',
                            subList: [
                                {name: '시험 내역', active: active, code: 'afterExam'},
                                {name: '간단 퀴즈 내역', active: active, code: 'afterQuiz'},
                                {name: '채팅 내역', active: active, code: 'afterChat'},
                                {name: '설문조사 내역', active: active, code: 'afterPoll'},
                                {name: '투표 내역', active: active, code: 'afterVote'},
                                {name: '공유 내역', active: active, code: 'afterData'},
                                {name: '게시판 내역', active: active, code: 'afterBoard'},
                                {name: '참가자 정보', active: active, code: 'afterUser'},
                            ]
                        },
                    ]
                }


            },
            setActive(name) {

                let routeName = '';
                if (name.indexOf('videoSettingData') !== -1) {
                    routeName = 'dataList/all';
                } else {
                    const string = name.replace('videoSetting', '');
                    const fc = string.charAt(0);
                    const lc = string.slice(1);
                    routeName = fc.toLowerCase() + lc;

                }
                if(this.mobile) {
                    if(this.code != 'info') {
                        for (let item of this.menuList) {
                            if (routeName == item.code) {
                                item.active = 'active';
                                this.code = item.code;
                            } else item.active = '';
                        }
                        if (!this.code) {
                            this.goPage('/video/setting/main');
                            return;
                        }
                    }
                } else {
                    for (let items of this.menuList) {
                        for (let item of items.subList) {
                            if (routeName == item.code) {
                                if (item.active != 'disable') item.active = 'active';
                                this.code = item.code;
                            } else if (item.code == 'dropDown') {
                                let checkActive = false;
                                for (let subItem of item.subList) {
                                    if (routeName == subItem.code) {
                                        if (subItem.active != 'disable') subItem.active = 'active';
                                        this.code = subItem.code;
                                        checkActive = true;
                                    } else if (subItem.active != 'disable') subItem.active = '';
                                }
                                if (checkActive) {
                                    if (item.active != 'disable') item.active = 'active'
                                    this.subMenuList = item.subList
                                } else {
                                    if (item.active != 'disable') item.active = '';
                                }

                            } else if (item.active != 'disable') item.active = '';
                        }
                    }
                }
            },
            setMenu(code, active) {
                if (active == 'disable') return;
                if (this.code == code) return;
                this.subMenuList = [];
                for (let items of this.menuList) {
                    for (let item of items.subList) {
                        if (code == item.code) {
                            if (item.active != 'disable') item.active = 'active';
                            if (code == 'dropDown') this.subMenuList = item.subList
                        } else if (item.active != 'disable') item.active = '';

                    }
                }
                if (code == 'dropDown') {
                    for (let subItem of this.subMenuList) {
                        subItem.active = '';
                    }
                    this.subMenuList[0].active = 'active';
                    this.$router.push('/video/setting/' + this.subMenuList[0].code)
                    this.code = this.subMenuList[0].code;
                } else {
                    this.code = code;
                    this.$router.push('/video/setting/' + this.code)
                }

            },
            setSubMenu(code) {
                if (this.code == code) return;
                for (let subItem of this.subMenuList) {
                    if (code == subItem.code) subItem.active = 'active';
                    else subItem.active = '';
                }
                this.$router.push('/video/setting/' + code)
                this.code = code;
            },
            goPage(url) {
                this.$router.push(url)
            },
            goMobileMain() {
                for (let item of this.menuList)item.active = '';
                this.menuShow = false;
                this.code = 'info';
                this.goPage('/video/setting/main')
            },
            goMobileMenu(code) {
                this.menuShow = false;
                for (let item of this.menuList) {
                    if (code == item.code) {
                        item.active = 'active';
                        this.code = item.code;
                    } else item.active = '';
                }
                this.$router.push('/video/setting/' + code)
            },
            playLive() {
                if (this.status == 'ready' || this.status == 'ing') {
                    let frm = {id: this.id.toString()};
                    this.$eventBus.$emit('overlay', 'show')
                    apiService.post(API_URL_BROADCAST_CHECK_LIVE_INFO, frm, (res) => {
                        this.$eventBus.$emit('overlay', 'hide')
                        switch (res.status) {
                            case 'success':
                                let width = JQ(window).width();
                                let height = JQ(window).height() + 200;
                                if (this.DEBUG) {
                                    window.open('/liveStream/hostLive/' + this.id, 'hostLiveWin');
                                } else {
                                    window.open('/liveStream/hostLive/' + this.id, 'hostLiveWin', 'width=' + width + ',height=' + height);
                                }

                                break;
                            case 'end':
                                let message = {
                                    message: '이미 종료된 방송입니다',
                                };
                                this.$eventBus.$emit('modalOpen', message);
                                break;
                        }

                    });


                } else {
                    let message = {
                        message: '이미 종료된 방송입니다',
                    };
                    this.$eventBus.$emit('modalOpen', message);

                }

            },
        },
        beforeDestroy() {
            this.setBroadcastInfoActive(false);
        }
    }
</script>


<style lang="scss" scoped>
    .menuBgScreen {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: calc(100vh);
        z-index: 110;
        background: rgba(0, 0, 0, 0.5);
    }

    #mobile-live-header-menu {
        position: fixed;
        top: 0;
        left: calc(-1 * (100vw - 90px));
        width: calc(100vw - 90px);
        height: calc(100vh);
        background: #fff;
        z-index: 111;
        padding: 40px 0 0 30px;

        .big-menu {
            color: #888;
            font-size: 13pt;
            font-weight: 700;
            margin-bottom: 15px;
            &.big-info {
                color:#000;
            }
        }

        .menu-ul {
            padding: 0;

            li {
                font-size: 14px;
                padding: 0 0 15px 20px;
                &.active {
                    font-weight: bold;
                    color:#000;
                }
            }
        }

    }

    .mobile-header {
        width: 100%;
        position: fixed;
        background: #141A2C;
        z-index: 90;
        top: 0;
        left: 0;
        height: 44px;
        display: flex;
        box-shadow: 0px 0px 9px #0000007A;

        .back-button {
            color: #fff;
            position: absolute;
            top: 10px;
            left: 15px;
        }

        .header-title {
            width: 100%;
            text-align: center;
            padding: 10px 0 0 15px;
            color: #fff;
            font-weight: 600;
        }
        .mypage {
            position: absolute;
            top: 13px;
            right: 45px;
            color: #fff;
        }
        .play-button {
            font-size: 13pt;
            position: absolute;
            top: 10px;
            right: 15px;
            color: #fff;
        }


    }

    .virtual-box {
        height: 24px;
    }

    .page-container {

        .setting-header {
            width: 100%;
            background: none;
            height: 75px;

            .header-container {
                position: relative;
                border-bottom: solid 1px #dfdfdf;

                .logo {
                    position: absolute;
                    top: 20px;
                    left: 30px;
                    cursor: pointer;
                }

                .menu-pannel {
                    width: 100%;
                    height: 75px;

                    .menu {
                        width: 100%;
                        display: flex;
                        justify-content: right;
                        padding: 20px 20px 0 0;

                        .menu-item {
                            padding: 0 20px 0 0;

                            .icon-box {
                                width: 40px;
                                height: 40px;
                                border: solid 1px #dfdfdf;
                                background: #fafafa;
                                border-radius: 50%;
                                text-align: center;
                                color: #999;
                                line-height: 45px;
                            }

                            .start-button {
                                width: 130px;
                                background: #1E90FF;
                                height: 40px;

                                span {
                                    color: #fff;
                                    font-size: 12pt;
                                }

                            }
                        }

                    }
                }
            }

        }

        .setting-body {
            display: flex;

            .setting-menu {
                min-width: 300px;
                min-height: calc(100vh - 75px);
                background: #f7f7fa;

                .step-menu-ul {
                    padding: 25px;

                    & > li {
                        .step-menu-item {
                            color: #999;
                            font-size: 15pt;
                            font-weight: bold;

                        }

                        .menu-ul {
                            padding: 15px 0 25px 0;

                            .menu-item {
                                padding: 8px 20px;
                                font-size: 14pt;
                                color: #333;
                                display: flex;
                                cursor: pointer;

                                .angle-icon {
                                    font-size: 14pt;
                                    margin: 5px 0 0 10px;

                                }

                                &.active {
                                    background: #1E90FF;
                                    color: #fff;
                                    border-radius: 10px;
                                }

                                &.disable {
                                    color: #999;
                                    cursor: default;

                                }


                            }

                            .sub-menu-ul {
                                .sub-menu-item {
                                    padding: 5px 10px;
                                    font-size: 12pt;
                                    cursor: pointer;

                                    &.active {
                                        color: #1E90FF;
                                        font-weight: bold;
                                    }

                                }
                            }
                        }
                    }
                }
            }

            .setting-content {
                width: 90%;
                padding: 40px 50px;

            }
        }
    }
</style>

<style lang="scss">
    .mypage-container {
        width: 100%;
        min-height: calc(100vh);
        background: #fff;

        .contents {
            .submit-button-box {
                display: flex;
                width: 700px;
                margin: 80px auto;
                justify-content: space-between;

                .cancle-button {
                    width: 48%;
                    padding: 0;
                    background: #999999;
                    color: #fff;
                    min-height: 70px;

                    span {
                        font-size: 14pt;
                    }
                }

                .submit-button {
                    width: 48%;
                    padding: 0;
                    background: #2D8CFF;
