            }


            .contents {
                padding: 0 15px 10px 15px;
                .sTitle {
                    font-size:12pt !important;
                    font-weight: bold;
                    padding: 0 0 10px 0;
                    color: #2D8CFF;
                    border-bottom: solid 1px #dcdcdc;

                    span.line {
                        font-size:12pt !important;
                        padding-bottom: 13px;
                        border-bottom: solid 2px #2D8CFF;
                    }

                    span.question-box {
                        border-bottom: solid 1px #999;
                        line-height: 6px;
                    }
                }

                .sub-title {
                    margin-top: 20px;
                    font-weight: bold;
                    color: #000;
                    font-size: 14px;
                }

                .sub-title-desc {
                    padding: 5px 0 20px 0;
                    font-size: 12px;
                    color: #555;

                }
                .submit-button-box {
                    display: flex;
                    width: 100%;
                    margin: 40px auto;
                    justify-content: space-between;

                    .cancle-button {
                        width: 48%;
                        border-radius: 0;
                        padding: 0;
                        background: #999999;
                        color: #fff;
                        min-height: 50px;
                        font-size: 11pt;
                    }

                    .submit-button {
                        width: 100%;
                        border-radius: 0;
                        padding: 0;
                        background: #2D8CFF;
                        color: #fff;
                        min-height: 50px;
                        font-size: 11pt;

                    }
                }


                .field-box {
                    padding: 0;

                    & > li {
                        border-bottom: solid 1px #DCDCDC;
                        display: block;
                        padding: 25px 0 0 0;

                        &:first-child {
                            border-top: none;
                        }

                        .required-icon {
                            width: 100%;
                            font-size: 14px;
                            padding-top: 5px;
                            padding-bottom: 10px;
                        }

                        .field-input {
                            width: 100%;

                            .switch-ul {
                                width: 100%;
                                padding: 0;
                                display: flex;
                                margin-bottom: 15px;

