                                    width: 100%;

                                    li {
                                        border-right: none;

                                        &:last-child {
                                            border-right: solid 1px #dcdcdc;
                                        }
                                    }
                                }

                                li {
                                    width: 50%;
                                    border: solid 1px #dcdcdc;
                                    text-align: center;
                                    line-height: 45px;
                                    height: 45px;
                                    font-size: 13px;
                                    cursor: pointer;

                                    &.active {
                                        background: #2D8CFF;
                                        color: #fff;
                                    }

                                    &:first-child {
                                        border-right: none;
                                        border-radius: 10px 0 0 10px;
